.card {
  border: 2px solid #57c4c8;
  padding: 10px;
  border-radius: 2em 1em 4em / 0.5em 3em;
  background-color: white;
  height: 175px;
  margin: 1rem;
  position: relative;
  float: left;
  width: 175px;
  display: block;
  -webkit-transition-duration: 0.4s;
       -o-transition-duration: 0.4s;
          transition-duration: 0.4s;
}
.card:hover {
    -webkit-box-shadow: 0 16px 18px 0 rgba(0,0,0,0.24),0 17px 50px 0 rgba(0,0,0,0.19);
            box-shadow: 0 16px 18px 0 rgba(0,0,0,0.24),0 17px 50px 0 rgba(0,0,0,0.19);
}
.card > .img-container {

  height: 100%;
  overflow: hidden;
}
.card > .img-container > img {

  width: 100%;
}



